import { DirektoriPegawaiKeluargaService } from 'src/app/sdk/hr/services/direktori-pegawai-keluarga.service'
import { Component, OnInit } from '@angular/core'
import { NgxSpinnerService } from 'ngx-spinner'
import { NzNotificationService } from 'ng-zorro-antd'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router } from '@angular/router'

import { FormControl, Validators } from '@angular/forms'
import { Pegawai_keluarga } from 'src/app/sdk/hr/models'
import { PegawaiDetailService } from '../../../service/pegawai-detail.service'

@Component({
    selector: 'app-add-keluarga',
    templateUrl: './add-keluarga.component.html',
    styleUrls: ['./add-keluarga.component.scss'],
})
export class AddKeluargaComponent implements OnInit {
    constructor(
        private direktoriPegawaiKeluargaService: DirektoriPegawaiKeluargaService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataKeluarga: Pegawai_keluarga

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.form()
    }

    dataForm: FormGroupTypeSafe<Pegawai_keluarga>
    form() {
        this.dataForm = this.fb.group<Pegawai_keluarga>({
            nama_keluarga: new FormControl('', [Validators.required]),
            jenis_kelamin_keluarga: new FormControl(''),
            tempat_lahir_keluarga: new FormControl(''),
            tanggal_lahir_keluarga: new FormControl(''),
            hubungan_keluarga: new FormControl(''),
            pendidikan_terakhir_keluarga: new FormControl(''),
            no_telepon_keluarga: new FormControl(''),
            status_perkawinan_keluarga: new FormControl(''),
            status_tanggungan: new FormControl(''),
            pekerjaan_keluarga: new FormControl(''),
            perusahaan_pekerjaan_keluarga: new FormControl(''),
            alamat_keluarga: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataKeluarga = this.dataForm.value
        this.direktoriPegawaiKeluargaService
            .postDirektoriPegawaiKeluargas(this.dataKeluarga)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/profile/biodata/keluarga/list'])
    }
}
