import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarEmergencyService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_emergency } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-emergency',
  templateUrl: './edit-emergency.component.html',
  styleUrls: ['./edit-emergency.component.scss']
})
export class EditEmergencyComponent implements OnInit {

    constructor(
        private direktoriPelamarEmergencyService: DirektoriPelamarEmergencyService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_pelamar: string = ''
    id_pelamar_emergency: string = ''
    data: Pelamar_emergency

    ngOnInit() {
        this.id_pelamar_emergency = this.activatedRoute.snapshot.paramMap.get('id_pelamar_emergency')
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.form()
        this.getData()
    }

    getData() {
        this.direktoriPelamarEmergencyService.getDirektoriPelamarEmergenciesId(this.id_pelamar_emergency).subscribe(
            data=>{
                this.dataForm.setValue(data.data)
            }
        )
    }
    dataForm: FormGroupTypeSafe<Pelamar_emergency>
    form() {
        this.dataForm = this.fb.group<Pelamar_emergency>({
            nama: new FormControl('', [Validators.required]),
            alamat: new FormControl(''),
            phone: new FormControl(''),
            hubungan: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pelamar_emergency: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPelamarEmergencyService
            .putDirektoriPelamarEmergenciesId({id: this.id_pelamar_emergency, body: this.data})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'keluarga', 'list'])
    }

}
