import { Pelamar_keluargalist } from 'src/app/sdk/hr/models/pelamar-_keluargalist';
import { PelamarServiceService } from './../../service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeluargaService, DirektoriPelamarEmergencyService } from 'src/app/sdk/hr/services';
import { Pelamar_keluarga, Pelamar_emergency } from 'src/app/sdk/hr/models';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { DirektoriPelamarKeluargalistService } from 'src/app/sdk/hr/services/direktori-pelamar-keluargalist.service';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPelamarKeluargaService: DirektoriPelamarKeluargaService,
        private direktoriPelamarEmergencyService: DirektoriPelamarEmergencyService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    dataKeluarga: Pelamar_keluarga[] = []
    dataEmergency: Pelamar_emergency[] = []

    id_pelamar: string = ''

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pelamar paja tu ' + this.id_pelamar)
            this.getKeluarga()
            this.getEmergency()
        })

    }

    getKeluarga() {
        this.direktoriPelamarKeluargaService.getDirektoriPelamarKeluargas(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.dataKeluarga = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPelamarKeluargaService
            .deleteDirektoriPelamarKeluargasId(id)
            .subscribe(data => {
                this.getKeluarga()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }


    getEmergency() {
        this.direktoriPelamarEmergencyService.getDirektoriPelamarEmergencies(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.dataEmergency = data.data
            }
        )
    }

    deleteE(id) {
        this.spinner.show()
        this.direktoriPelamarEmergencyService
            .deleteDirektoriPelamarEmergenciesId(id)
            .subscribe(data => {
                this.getEmergency()
                this.spinner.hide()
            })
    }


}
