import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarEmergencyService, DirektoriPegawaiEmergencyService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_emergency, Pegawai_emergency } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';


@Component({
  selector: 'app-edit-emergency',
  templateUrl: './edit-emergency.component.html',
  styleUrls: ['./edit-emergency.component.scss']
})
export class EditEmergencyComponent implements OnInit {

    constructor(
        private direktoriPegawaiEmergencyService: DirektoriPegawaiEmergencyService,
        private pegawaiDetailService: PegawaiDetailService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_pegawai: string = ''
    id_pegawai_emergency: string = ''
    data: Pegawai_emergency = {}

    ngOnInit() {
        this.id_pegawai_emergency = this.activatedRoute.snapshot.paramMap.get('id')
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
        })
        this.form()
        this.getData()
    }

    getData() {
        this.direktoriPegawaiEmergencyService.getDirektoriPegawaiEmergenciesId(this.id_pegawai_emergency).subscribe(
            data=>{
                this.dataForm.setValue(data.data)
            }
        )
    }
    dataForm: FormGroupTypeSafe<Pegawai_emergency>
    form() {
        this.dataForm = this.fb.group<Pegawai_emergency>({
            nama: new FormControl('', [Validators.required]),
            alamat: new FormControl(''),
            phone: new FormControl(''),
            hubungan: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pegawai_emergency: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPegawaiEmergencyService
            .putDirektoriPegawaiEmergenciesId({id: this.id_pegawai_emergency, body: this.data})
            .subscribe(
                data => {
                       this.spinner.hide()
                       this.goToBack()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/employee/pegawai-detail', this.id_pegawai,'biodata', 'keluarga', 'list'])
    }
}

