import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { KeluargaRoutingModule } from './keluarga-routing.module';
import { ListComponent } from './list/list.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { AddEmergencyComponent } from './add-emergency/add-emergency.component';
import { EditEmergencyComponent } from './edit-emergency/edit-emergency.component';


@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent, AddEmergencyComponent, EditEmergencyComponent],
    imports: [
        CommonModule,
        KeluargaRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
    ],
    providers: [FormBuilderTypeSafe],
})
export class KeluargaModule { }
