import { PelamarServiceService } from './../../service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeluargalistService } from 'src/app/sdk/hr/services/direktori-pelamar-keluargalist.service';
import { Pelamar_keluargalist } from 'src/app/sdk/hr/models/pelamar-_keluargalist';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

        constructor(
            private direktoriPelamarKeluargalistService: DirektoriPelamarKeluargalistService,
            private spinner: NgxSpinnerService,
            private notification: NzNotificationService,
            private fb: FormBuilderTypeSafe,
            private pelamarServiceService: PelamarServiceService,
            private pesan: PesanService,
            private router: Router,
        ) {}

        id_pelamar: string = ''
        dataKeluarga: Pelamar_keluargalist

        ngOnInit() {
            this.pelamarServiceService.getIdPelamar().subscribe(data => {
                this.id_pelamar = data
            })
            this.form()
        }

        dataForm: FormGroupTypeSafe<Pelamar_keluargalist>
        form() {
            this.dataForm = this.fb.group<Pelamar_keluargalist>({
                nama_keluarga: new FormControl('', [Validators.required]),
                jenis_kelamin_keluarga: new FormControl(''),
                tempat_lahir_keluarga: new FormControl(''),
                tanggal_lahir_keluarga: new FormControl(''),
                hubungan_keluarga: new FormControl(''),
                no_telepon_keluarga: new FormControl(''),
                pekerjaan_keluarga: new FormControl(''),
                perusahaan_pekerjaan_keluarga: new FormControl(''),
                alamat_keluarga: new FormControl(''),
                id_pelamar: new FormControl(this.id_pelamar),
            })
        }

        simpan() {
            this.spinner.show()
            if (this.dataForm.invalid) {
                this.pesan.pesanWarningForm('Form Tidak Valid')
                this.spinner.hide()
                return
            }

            this.dataKeluarga = this.dataForm.value
            this.direktoriPelamarKeluargalistService
                .postDirektoriPelamarKeluargalists(this.dataKeluarga)
                .subscribe(
                    data => {
                        this.goToBack()
                        this.spinner.hide()
                    },
                    err => {
                        this.pesan.pesanWarningForm('Gagal di simpan')
                        this.spinner.hide()
                    },
                    () => {
                        console.log('HTTP request completed.')
                    },
                )
        }

        goToBack() {
            this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'keluarga', 'list'])
        }


}
