import { Pelamar_keluargalist } from 'src/app/sdk/hr/models/pelamar-_keluargalist';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeluargaService, DirektoriPelamarEmergencyService, DirektoriPegawaiKeluargaService, DirektoriPegawaiEmergencyService } from 'src/app/sdk/hr/services';
import { Pelamar_keluarga, Pelamar_emergency, Pegawai_keluarga, Pegawai_emergency } from 'src/app/sdk/hr/models';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiKeluargaService: DirektoriPegawaiKeluargaService,
        private direktoriPegawaiEmergencyService: DirektoriPegawaiEmergencyService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    dataKeluarga: Pegawai_keluarga[] = []
    dataEmergency: Pegawai_emergency[] = []

    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
             this.getKeluarga()
            this.getEmergency()
        })

    }

    getKeluarga() {
        this.direktoriPegawaiKeluargaService.getDirektoriPegawaiKeluargas(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataKeluarga = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiKeluargaService
            .deleteDirektoriPegawaiKeluargasId(id)
            .subscribe(data => {
                this.getKeluarga()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }


    getEmergency() {
        this.direktoriPegawaiEmergencyService.getDirektoriPegawaiEmergencies(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataEmergency = data.data
            }
        )
    }

    deleteE(id) {
        this.spinner.show()
        this.direktoriPegawaiEmergencyService
            .deleteDirektoriPegawaiEmergenciesId(id)
            .subscribe(data => {
                this.getEmergency()
                this.spinner.hide()
            })
    }


}
