import { EditKeluargaComponent } from './edit-keluarga/edit-keluarga.component';
import { AddKeluargaComponent } from './add-keluarga/add-keluarga.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListComponent } from './list/list.component';


const routes: Routes = [
    {
        path:'',
        redirectTo:'list'
    },
    {
        path: 'edit/:id_pegawai_pegawai_keluarga',
        component: EditKeluargaComponent
    },
    {
        path: 'add-keluarga',
        component: AddKeluargaComponent
    },
    {
        path: 'list',
        component: ListComponent
    },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class KeluargaRoutingModule { }
